import React from "react"
import PropTypes from "prop-types";
import { graphql, useStaticQuery, Link } from "gatsby"
import classNames from "classnames";

import GridContainer from "@components/Grid/GridContainer.js";
import GridItem from "@components/Grid/GridItem.js";

import slugify from "slugify"
// import ThreeMysticApesUtilities from "three_mystic_apes-extension/util/utilities.js"
import { H3, H6 } from "@components/header/header.js";

import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';

import Moment from 'moment';

BlogTag.propTypes = {
    props: PropTypes.object,
}


export default function BlogTag({ children, pageNode, location, ...props }) {
    const themeVariation = (typeof window !== 'undefined') ? (localStorage.getItem('themeVariation') ? localStorage.getItem('themeVariation') : "dark") : "dark";
    const postGroup = "tag";

    const postGroupValue = props[postGroup] === null || typeof props[postGroup] === 'undefined' ? "" : props[postGroup];
    const { className } = props;
    const { distinctList } = useStaticQuery(
        graphql`
                query {
                    distinctList: allThreeMysticApesPost(filter: {showNode: {eq: true}}) {
                        list: distinct(field: post___tags)
                    }                    
                }
            `)
    const postListGrid = "postListGrid"

    const postsGridListClassNames = classNames({
        [postListGrid]: true,
        [themeVariation]: true,
        [className]: className
    });
    
    if (postGroupValue === "") {

        return (
            <GridContainer justify="center">
                {distinctList.list.map((item, index) => (
                    <GridItem cs={12} sm={6} md={4} key={index}>
                        <Link
                            to={`/blog/${postGroup}/${slugify(item, {
                                remove: /[^\w\s$*_+~.()'"!\-:@/]/g, // this is the set of allowable characters
                            }).toLowerCase()}`}
                            title={item}
                            aria-label={item}
                        >
                            <div>
                                {item}
                            </div>
                        </Link>
                    </GridItem>
                ))}
            </GridContainer>
        )

    }

    return (
        <GridContainer justify="center">
            <GridItem cs={12} sm={12} md={12}>
                <GridList cellHeight={180}>
                    {props.posts.edges.map(({ node }, index) => (
                        <GridListTile key={index} className={postsGridListClassNames} >
                            <Link
                                to={node.slug}
                                title={node.post.title}
                                aria-label={node.post.ariaLabel}
                                state={{ crum: { pageNode: pageNode, locationSlug: location.pathname.trimRight("/,\\"), postGroupValue } }}
                            >
                                <img src={node.post.hero.publicURL} alt={node.post.title} />
                                <GridListTileBar
                                    title={<H3>{node.post.title}</H3>}
                                    subtitle={<div>
                                        <H6>{node.post.excerpt}</H6>
                                        <div>{Moment(node.post.startDate).format('d MMM')}</div>
                                    </div>}
                                />
                            </Link>
                        </GridListTile>
                    ))}
                </GridList>
            </GridItem>
        </GridContainer>
    )
}